@import url(https://fonts.googleapis.com/css?family=Fjalla+One);
@import url(https://fonts.googleapis.com/css?family=Fjalla+One);
@import url(https://fonts.googleapis.com/css?family=Fjalla+One);
@import url(https://fonts.googleapis.com/css?family=Fjalla+One);
body {
  margin: 0;
  font-family: -apple-system, BlinkMacSystemFont, "Segoe UI", "Roboto", "Oxygen",
    "Ubuntu", "Cantarell", "Fira Sans", "Droid Sans", "Helvetica Neue",
    sans-serif;
  -webkit-font-smoothing: antialiased;
  -moz-osx-font-smoothing: grayscale;
}

code {
  font-family: source-code-pro, Menlo, Monaco, Consolas, "Courier New",
    monospace;
}

.App {
  text-align: center;
}

body {
  background: url("https://i.imgur.com/32jDJWN.jpg") no-repeat center center fixed;
  -webkit-background-size: cover;
  -moz-background-size: cover;
  -o-background-size: cover;
};

header {
  display: flex;
  justify-content: space-evenly;
  font-family: "Fjalla One", sans-serif;
}

nav {
  background-color: #076d4d;
  padding: 10px;
}

a {
  -webkit-text-decoration: "none";
          text-decoration: "none";
  color: "#E0B564";
  font-size: "18px";
}

.flex-wrapper {
  display: flex;
  flex-wrap: wrap;
  justify-content: center;
}

.home h1 {
  margin: auto 0;
}

.home h2 {
  font-size: 35px;
  font-family: "Fjalla One", sans-serif;
  color: #076d4d;
  padding: 25px;
  padding-right: 20px;
  text-shadow: 0.5px 2px #e0b564;
}

.home p {
  font-family: "Fjalla One", sans-serif;
  font-size: 30px;
  color: #076d4d;
}

.home input {
  background-color: #84182b;
  color: white;
  font-family: "Fjalla One", sans-serif;
  font-size: 15px;
  border: none;
  width: 15em;
  justify-content: center;
  margin-left: 15%;
}

.home label {
  font-family: "Fjalla One", sans-serif;
  color: #076d4d;
  margin-bottom: 20px;
  display: flex;
  font-size: 35px;
  height: 50%;
  justify-content: center;
  text-shadow: 0.5px 2px #e0b564;
}

.home label,
input {
  justify-content: center;
}

.home footer {
  position: absolute;
  left: 0;
  bottom: 0;
  width: 100%;
  background-color: #076d4d;
}

.home h3 {
  color: #e0b564;
  font-family: "Fjalla One", sans-serif;
  font-size: 20px;
}

.home form {
  display: flex;
  justify-content: center;
  flex-direction: column;
}

.about {
  text-shadow: 0.5px 2px #e0b564;
}

.vendor-list {
  width: 90%;
  margin: 0 auto;
  display: grid;
  grid-gap: 5px;
  grid-template-columns: 1fr 1fr 1fr;
}

.vendor-item {
  padding: 5px;
  background-color: rgba(255, 255, 255, 0.4);
}

.vendor-item h2 {
  margin: 0;
  font-family: "Fjalla One", sans-serif;
}

.vendor-item p {
  margin: 0;
  padding-top: 15px
}

.vendors h1 {
  color: #076D4D ;
  text-shadow: 0.5px 2px #e0b564;
  font-size: 50px;
}

.vendors p {
  color: #076D4D;
  font-family: "Fjalla One", sans-serif;
  font-size: 20px;
}

.vendors h2 {
  color: #84182B;
}

.vendors button {
  font-family: "Fjalla One", sans-serif;
  font-size: 14px;
  color: white;
  background-color:#84182B;
  border-color: #076D4D;
}

.vendor-item ul {
  list-style-type: none;
  padding-right: 40px;
  font-family: "Fjalla One", sans-serif;
  color: #84182B;
  font-size: 18px;
}

.vendor-item h3 {
  font-family: "Fjalla One", sans-serif;
  font-size: 18px;
  color: #84182B
}



.basic-info {
  display: flex;
  flex-direction: column;
  align-items: center;
}

.filters {
  display: flex;
  justify-content: center;
}

.info h1 {
  font-family: "Fjalla One", sans-serif;
  color: #076d4d;
  font-size: 50px;
  text-shadow: 0.5px 2px #e0b564;
}

.info label {
  font-family: "Fjalla One", sans-serif;
  color: #076d4d;
  text-shadow: 0.5px 2px #e0b564;
  font-size: 25px;
}

.days-filter div {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  padding-top: 5px;
  padding-right: 225px;
  justify-content: space-evenly;
}

.submit-button {
  background-color: #84182b;
  color: white;
  font-family: "Fjalla One", sans-serif;
  font-size: 20px;
  padding-left: 20px;
  padding-right: 20px;
  text-align: center;
}

.products-dropdown {
  display: flex;
  flex-direction: column;
  flex-wrap: wrap;
  padding-bottom: 175px;
  padding-left: 700px;
  justify-content: space-evenly;
}

.row {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
}

.column {
  display: flex;
  flex-direction: column;
  flex-basis: 100%;
  flex: 1 1;
  padding-right: 250px;
}

.days {
  padding-right: 250px;
}

.category {
  padding-top: 250px;
}

input {
  border: none;
  background-color: #84182b;
  color: white;
  font-family: "Fjalla One", sans-serif;
  font-size: 15px;
  padding-left: 20px;
  padding-right: 20px;
}

.edit-vendor-header h1 {
  font-size: 35px;
  font-family: "Fjalla One", sans-serif;
  color: #076d4d;
  padding: 25px;
  padding-right: 20px;
  text-shadow: 0.5px 2px #e0b564;
}

.edit-vendor-input {
  padding: 5px;
  margin: 5px;
}

.vendor-edit-label {
  font-size: 20px;
  font-family: "Fjalla One", sans-serif;
  color: #076d4d;
  text-shadow: 0.5px 2px #e0b564;
}

.vendor-edit-category {
  display: flex;
  justify-content: center;
}

.vendor-edit-product {
  display: block;
  margin: 5px;
}

.vendor-edit-submit-button {
  margin: 10px;
}

.vendor-edit-category {
  font-family: "Fjalla One", sans-serif;
  color: #076d4d;
  text-shadow: 0.5px 2px #e0b564;
  font-size: 25px;
}

.vendor-edit-days {
  font-family: "Fjalla One", sans-serif;
  color: #076d4d;
  text-shadow: 0.5px 2px #e0b564;
  font-size: 28px;
}

.enter-product {
  font-family: "Fjalla One", sans-serif;
  color: #076d4d;
  text-shadow: 0.5px 2px #e0b564;
  font-size: 20px;
  margin: 5px;
}

.edit-vendor-add {
  margin: 5px;
}

body {
  overflow-x: hidden;
}

/* hover effect credit */
/* https://codepen.io/robertkirsz/pen/ZvorjB */

.maps-container {
  display: block;
}

.map-label {
  display: flex;
  justify-content: center;
}

.maps-container img {
  width: 100%;
  height: 350px;
  display: block;
  pointer-events: none;
}

.row-one {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
  margin-left: -12px;
}

.row-two {
  display: flex;
  flex-direction: row;
  flex-wrap: wrap;
  width: 100%;
}

.maps-column {
  display: flex;
  flex-direction: column;
  flex-basis: 100%;
  flex: 1 1;
}

.map-label label {
  font-size: 32px;
  display: flex;
  justify-content: center;
  font-family: "Fjalla One", sans-serif;
  color: #076d4d;
  padding: 25px;
  text-shadow: 0.5px 2px #e0b564;
}

figure {
  width: 400px;
  background-repeat: no-repeat;
}

figure:hover img {
  opacity: 0;
}

